<template>
  <div class="order">
    <foodHeaderNav title="开单" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>

    <div class="sales-points-container">
      <p class="sales-points-num">{{seatsInfo.code}}</p>
      <p class="sales-points-desc">{{seatsInfo.pcDescript}}</p>
    </div>
    <div class="tp-list">
      <div class="tp-item" :class="itemActive==1?'tp-item-active':''" @click="orderSwitch('1')">预订单</div>
      <div
        class="tp-item tp-item-middle"
        :class="itemActive==2?'tp-item-active':''"
        @click="orderSwitch('2')"
      >现做订单</div>
      <div
        class="tp-item"
        :class="itemActive==3?'tp-item-active':''"
        @click="orderSwitch('3')"
      >进行中订单({{orderNum}})</div>
    </div>
    <div class="order-box">
      <div class="order-container pre-order-container" v-show="itemActive==1">
        <div class="pre-order-item order-item">
          <span class="label">就餐人数</span>
          <input type="number" class="persenNum" placeholder="人数（必填）" v-model="preOrder.peopleNum">
        </div>
        <div class="dividing-line"></div>
        <div class="pre-order-item order-item">
          <span class="label">就餐日期</span>
          <gpicker ref="preOrderDate" @change="changePreOrderDate()"></gpicker>
        </div>
        <div class="pre-order-item order-item">
          <span class="label">就餐时间</span>
          <gpicker ref="preOrderTime" @change="changePreOrderTime()"></gpicker>
        </div>
        <div class="dividing-line"></div>
        <div class="pre-order-item order-item">
          <span class="label">服务员</span>
          <div class="waiter-name">{{preOrder.waiter}}</div>
        </div>
        <!-- <div class="pre-order-item order-item">
          <span class="label">统计码</span>
          <gpicker ref="preOrderStatistics" @change="changePreOrderStatistics()"></gpicker>
        </div>-->
      </div>
      <div class="order-container now-order-container" v-show="itemActive==2">
        <div class="now-order-item order-item">
          <span class="label">就餐人数</span>
          <input type="number" class="persenNum" placeholder="人数（必填）" v-model="nowOrder.peopleNum">
        </div>
        <div class="dividing-line"></div>
        <div class="now-order-item order-item">
          <span class="label">服务员</span>
          <div class="waiter-name">{{nowOrder.waiter}}</div>
        </div>
        <!-- <div class="now-order-item order-item">
          <span class="label">统计码</span>
          <gpicker ref="nowOrderStatistics" @change="changeNowOrderStatistics()"></gpicker>
        </div>-->
      </div>

      <div class="order-container processing-order-container" v-show="itemActive==3">
        <div class="tp-list">
          <div class="tp-item">总金额：{{totalAmount}}</div>
          <div class="tp-item tp-item-middle" style="margin-left:0px;">
            待支付金额：
            <span class="waitPay">{{unpaidAmount}}</span>
          </div>
          <div class="tp-item mergePay" v-if="unpaidAmount!=0" @click="mergePay()">合并支付</div>
        </div>
        <div class="processing-order-content" v-for="(item, index) in orderList" :key="index">
          <div class="processing-order-item" v-if="item.orderSta !=2">
            <div class="checkbox-container">
              <template v-if="item.orderSta==1">
                <input
                  class="checkbox"
                  type="checkbox"
                  v-if="item.checked"
                  v-model="item.checked"
                  :value="item.accnt"
                  @click="checkedFun(false,item)"
                >
                <input
                  class="checkbox"
                  type="checkbox"
                  v-else
                  v-model="item.checked"
                  :value="item.accnt"
                  @click="checkedFun(true,item)"
                >
              </template>
            </div>
            <div class="processing-order-create-order-time">
              <p class="processing-order-title">下单时间</p>
              <p class="processing-order-details">{{item.orderTime|dateFormat}}</p>
            </div>
            <span class="horizontal-dividing-line"></span>
            <div class="processing-order-meal-time">
              <p class="processing-order-title">就餐时间</p>
              <p class="processing-order-details">{{item.dinnerTime|dateFormat}}</p>
            </div>
            <span class="horizontal-dividing-line"></span>
            <div
              v-if="item.orderSta==1"
              class="processing-order-status processing-order-wait-pay"
              @click="gotoOrderFun(item)"
            >待支付</div>
            <div
              v-else-if="item.orderSta==-1"
              class="processing-order-status processing-order-failed"
              @click="gotoOrderFun(item)"
            >下单失败</div>
            <div
              v-else-if="item.orderSta==0"
              class="processing-order-status processing-order-not-cooking"
              @click="gotoOrderFun(item)"
            >未下厨</div>
          </div>
        </div>
      </div>
    </div>
    <div class="foot" v-show="itemActive==1||itemActive==2">
      <div class="cancel foot-btn" @click="gotoBack()">取 消</div>
      <div class="confirm foot-btn" @click="createOrder()">开 单</div>
    </div>
    <foodPayChoise v-if="isShowPay" :isManyOrder="true" @hidePage="hidePage($event)"></foodPayChoise>
  </div>
</template>

<script>
import moment from "moment";
import footTabs from "../common/footTabs";
import gpicker from "../common/gpicker";
import { mapState } from "vuex";
import { Indicator, Toast, MessageBox } from "mint-ui";
import foodHeaderNav from "../common/foodHeaderNav";
import foodPayChoise from "../common/foodPayChoise";
export default {
  name: "index",
  components: {
    footTabs,
    gpicker,
    foodHeaderNav,
    foodPayChoise
  },
  data() {
    return {
      itemActive: 1,
      preOrder: {
        peopleNum: 1,
        date: null,
        time: null,
        waiter: null
      },
      nowOrder: {
        peopleNum: 1,
        waiter: null
      },
      orderList: [],
      orderNum: 0,
      totalAmount: 0,
      unpaidAmount: 0,
      isShowPay: false,
      printNum: 0,
      payMethod: {},
      orderCheckedList: [],
      orderCheckedIndex: 0,
      singleOrderInfo: {}
    };
  },
  computed: {
    ...mapState({
      seatsInfo: state => state.food.seatsInfo
    })
  },
  created() {
    if (localStorage.orderTypes) {
      if (localStorage.orderTypes == "nowOrder") {
        this.itemActive = 2;
      } else {
        this.itemActive = 1;
      }
    }
    this.$store.commit("clearCart");
    this.$store.commit("clearOrderNum");
    console.log("this.seatsInfo:", this.seatsInfo);
    this.getOrderList();
  },
  mounted() {
    this.preOrder.waiter = localStorage.waiter;
    this.nowOrder.waiter = localStorage.waiter;
    let orderDay = JSON.parse(localStorage.foodUserInfo).orderDay;
    let preOrderDate = [];

    for (let i = 0; i <= orderDay; i++) {
      let Child = {
        code: moment()
          .add("days", i)
          .format("YYYY-MM-DD"),
        name: moment()
          .add("days", i)
          .format("MM-DD")
      };
      preOrderDate.push(Child);
    }
    let todayCode = moment()
      .add("days", 1)
      .format("YYYY-MM-DD");

    this.$refs.preOrderDate.setData(preOrderDate, todayCode);

    // this.$refs.preOrderStatistics.setData(
    //   preOrderstatistics,
    //   this.preOrder.time
    // );
    // this.$refs.nowOrderStatistics.setData(
    //   nowOrderStatistics,
    //   this.nowOrder.time
    // );
    let preOrderTime = [
      { code: "08:00", name: "早餐" },
      { code: "12:00", name: "中餐" },
      { code: "17:00", name: "晚餐" }
    ];
    let timeStr = "08:00";
    let eatTime = localStorage.eatTime;
    if (eatTime) {
      eatTime = JSON.parse(eatTime);
      if (eatTime["s" + this.seatsInfo.pccode]) {
        let time = "";
        eatTime["s" + this.seatsInfo.pccode].forEach(ele => {
          if (ele.table == this.seatsInfo.code) {
            time = ele.time;
          }
        });
        if (time) {
          if (time == "08:00") {
            timeStr = "12:00";
          } else if (time == "12:00") {
            timeStr = "17:00";
          } else {
            timeStr = "08:00";
          }
        }
      }
    }
    this.$refs.preOrderTime.setData(preOrderTime, timeStr);
    this.preOrder.date = this.$refs.preOrderDate.getValue();
    this.preOrder.time = this.$refs.preOrderTime.getValue();
  },
  methods: {
    //返回上一页
    gotoBack() {
      this.$router.push("/foodIndex");
    },
    gotoOrderFun(item) {
      this.$store.commit("submitOrderNum", {
        orderNum: item.accnt,
        from: "createOrder"
      });
      this.$router.push("/foodOrder");
    },
    createOrder() {
      let orderInfoObj;
      if (this.itemActive == 1) {
        orderInfoObj = this.preOrder;
      } else {
        orderInfoObj = this.nowOrder;
      }
      if (orderInfoObj.peopleNum > 99) {
        this.msg("就餐人数过多！");
        return;
      }
      for (const key in orderInfoObj) {
        if (orderInfoObj.hasOwnProperty(key)) {
          const element = orderInfoObj[key];
          if (!element) {
            this.msg("请填写人数或日期！");
            return;
          }
          // console.log(element);
        }
      }
      this.$store.commit("submitCreateOrderInfo", orderInfoObj);
      this.$router.push("/foodMenu");
    },
    orderSwitch(val) {
      this.itemActive = val;
    },
    changePreOrderDate() {
      this.preOrder.date = this.$refs.preOrderDate.getValue();

      this.$refs.preOrderTime.clear();
      let preOrderTime = [];

      let today = moment().format("YYYY-MM-DD");
      let hh = Number.parseInt(moment().format("HH"));
      //console.log(hh);
      if (this.preOrder.date > today) {
        // for (let i = 0; i < 24; i++) {
        //   preOrderTime.push(...this.formatTime(i));
        // }
        preOrderTime = [
          { code: "08:00", name: "早餐" },
          { code: "12:00", name: "中餐" },
          { code: "17:00", name: "晚餐" }
        ];
      } else {
        // for (let i = hh + 1; i < 24; i++) {
        //   preOrderTime.push(...this.formatTime(i));
        // }
        if (hh < 8) {
          preOrderTime = [
            { code: "08:00", name: "早餐" },
            { code: "12:00", name: "中餐" },
            { code: "17:00", name: "晚餐" }
          ];
        } else if (hh >= 8 && hh < 12) {
          preOrderTime = [
            { code: "12:00", name: "中餐" },
            { code: "17:00", name: "晚餐" }
          ];
        } else {
          preOrderTime = [{ code: "17:00", name: "晚餐" }];
        }
      }

      this.$refs.preOrderTime.setData(preOrderTime, "08:00"); //this.preOrder.time
      ///console.log("this.preOrder.date:", this.preOrder.date);
    },
    changePreOrderTime() {
      this.preOrder.time = this.$refs.preOrderTime.getValue();
    },

    checkedFun(bc, item) {
      // this.msg(item.checked);
      if (bc) {
        this.unpaidAmount = Number.parseFloat(
          Number.parseFloat(this.unpaidAmount) + Number.parseFloat(item.amount)
        ).toFixed(2);
      } else {
        //console.log(item.amount);
        this.unpaidAmount = Number.parseFloat(
          Number.parseFloat(this.unpaidAmount) - Number.parseFloat(item.amount)
        ).toFixed(2);
      }
    },
    hidePage(obj) {
      this.isShowPay = false;
      if (obj.state == "confirmPay") {
        //this.getOrderList();
        //this.print();
        this.payMethod = obj.item;
        this.payFun();
      }
    },
    getOrderList() {
      this.totalAmount = 0;
      this.unpaidAmount = 0;
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "getMasterList",
            pccode: this.seatsInfo.pccode,
            tableNo: this.seatsInfo.code,
            isGoing: true
          },
          vv: 5
        })
        .then(res => {
          console.log("denglu:", res);
          if (res.data.code == "0") {
            // moment;
            this.orderList = res.data.data;
            this.redoSum();
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    redoSum() {
      this.unpaidAmount = 0;
      this.totalAmount = 0;
      this.orderList.forEach(ele => {
        if (ele.orderSta != 2) {
          this.orderNum++;

          this.totalAmount = Number.parseFloat(
            Number.parseFloat(this.totalAmount) + Number.parseFloat(ele.amount)
          ).toFixed(2);
        }
        if (ele.orderSta == 1) {
          this.$set(ele, "checked", true);

          this.unpaidAmount = Number.parseFloat(
            Number.parseFloat(this.unpaidAmount) + Number.parseFloat(ele.amount)
          ).toFixed(2);
        }
      });
    },
    mergePay() {
      this.orderCheckedList = this.orderList.filter(item => item.checked);
      this.orderCheckedList = this.orderCheckedList.reverse();
      this.isShowPay = true;
    },
    print() {
      //console.log("dayin:", this.menuInfo);
      this.printNum = 0;
      printDcData(this.printSuccess, this.printError, this.singleOrderInfo);
    },
    printSuccess() {
      // if (this.printNum < 1) {
      //   this.printNum++;
      //   printDcData(this.printSuccess, this.printError, this.singleOrderInfo);
      // } else {
      if (this.orderCheckedIndex + 1 < this.orderCheckedList.length) {
        this.orderCheckedIndex++;
        this.payFun();
      } else {
        this.getOrderList();
      }
      // }
    },
    printError(v) {
      MessageBox.confirm("打印出错：" + v + ",继续打印？")
        .then(action => {
          printDcData(this.printSuccess, this.printError, this.singleOrderInfo);
        })
        .catch(action => {});
    },
    payFun() {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "updateMaster",
            accnt: this.orderCheckedList[this.orderCheckedIndex].accnt,
            presta: 0,
            payCode: "TAIKANG-" + this.payMethod.payCode,
            outId: 1,
            orderNo: 1
          },
          vv: 5
        })
        .then(res => {
          console.log("denglu:", res);
          if (res.data.code == "0") {
            this.msg("支付成功！");

            this.getOrderInfo(this.orderCheckedList[this.orderCheckedIndex]);
          } else {
            this.msg2(res.data.msg);
            this.getOrderList();
          }
        });
    },
    getOrderInfo(item) {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "getMasterInfo",
            accnt: item.accnt
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            this.singleOrderInfo = res.data.data;
            this.singleOrderInfo.payCode = "TAIKANG-" + this.payMethod.payCode;
            this.singleOrderInfo.payName = this.payMethod.payName;
            console.log("----------:", this.singleOrderInfo);
            if (
              this.singleOrderInfo.eatTime <
              moment()
                .add(10, "minute")
                .format("YYYY-MM-DD HH:mm")
            ) {
              this.pushOrder(this.orderCheckedList[this.orderCheckedIndex]);
            }
            this.print();
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    pushOrder(order) {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "sendOrder",
            v: "2.0",
            accnt: order.accnt
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            //this.msg(res.data.msg);
            this.msg("已下厨");
          } else {
            this.msg2(res.data.msg);
          }
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    msg2(msg) {
      MessageBox.alert(msg);
    }
  },
  filters: {
    dateFormat(val) {
      return (
        val.substring(5, 7) +
        "/" +
        val.substring(8, 10) +
        " " +
        val.substring(11, 16)
      );
    }
  }
};
</script>
<style lang='less'>
.order {
  .sales-points-container {
    padding: 0.587rem 0;
    background-color: #fff;
    .sales-points-num {
      font-size: 0.96rem;
      color: #333333;
      text-align: center;
    }
    .sales-points-desc {
      font-size: 0.427rem;
      color: #999999;
      text-align: center;
    }
  }
  .tp-list {
    display: flex;
    justify-content: space-around;
    padding-top: 9px;
    margin-top: 1px;
    margin-bottom: 0.133rem;
    background-color: #fff;
    .tp-item {
      padding-bottom: 0.133rem;
      border-bottom: 3px solid #fff;
    }
    .tp-item-middle {
      margin-left: 1.067rem;
      .waitPay {
        font-size: 16px;
        color: #ff2233;
      }
    }
    .tp-item-active {
      border-bottom-color: #f69c2d;
    }
    .mergePay {
      width: 60px;
      height: 0.587rem;
      line-height: 0.587rem;
      border-radius: 3px;
      background: #ffdbdf;
      color: #ff2233;
      padding-bottom: 0;
    }
  }
  .order-box {
    background-color: #fff;
    .order-container {
      padding: 0.347rem 0.32rem;
      .dividing-line {
        height: 0;
        width: 100%;
        border-bottom: 1px dashed #dddddd;
        margin-bottom: 0.373rem;
      }
      .order-item {
        display: flex;
        margin-bottom: 0.373rem;
        align-items: center;
        .label {
          width: 105px;
          text-align: left;
        }
        .persenNum {
          height: 0.9rem;
          flex: 1;
          font-size: 0.32rem;
          color: #999999;
          border: 1px solid #dddddd;
          padding: 0 0.347rem;
        }
        .waiter-name {
          padding: 0 0.13333333rem;
          float: right;
          flex: 1;
          box-sizing: border-box;
          height: 0.9rem;
          line-height: 0.9rem;
          background: #fff;
          border: 1px solid #e8e8e8;
          border-radius: 2px;
          text-align: left;
        }
      }
      .processing-order-content:nth-child(2n) {
        background: #f4f5f7;
      }
      .processing-order-item {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 60px;
        .horizontal-dividing-line {
          display: inline-block;
          width: 1px;
          height: 30%;
          background-color: #cccccc;
        }
        .processing-order-title {
          font-size: 0.32rem;
          color: #999999;
          text-align: left;
        }
        .processing-order-details {
          //font-size: 0.533rem;
          font-size: 16px;
        }
        .processing-order-create-order-time {
          .processing-order-details {
            color: #333333;
          }
        }
        .processing-order-meal-time {
          .processing-order-details {
            color: #70a338;
          }
        }
        .processing-order-status {
          width: 60px;
          height: 0.587rem;
          line-height: 0.587rem;
          border-radius: 3px;
        }
        .processing-order-wait-pay {
          background: #dbebff;
          color: #467cbb;
        }
        .processing-order-failed {
          background: #ffdbdf;
          color: #ff2233;
        }
        .processing-order-not-cooking {
          background: #f3d824;
          color: #333333;
        }
        .processing-order-cooking {
        }
        .checkbox-container {
          width: 20px;
          .checkbox {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
    .gpicker {
      position: relative;
      padding: 0 0.13333333rem;
      float: right;
      flex: 1;
      box-sizing: border-box;
      height: 0.9rem;
      background: #fff;
      border: 1px solid #e8e8e8;
      border-radius: 2px;

      .ginput {
        border: none;
        width: 100%;
        height: 0.82rem;
        box-sizing: border-box;
        text-align: left;
        border-radius: 2px;
        border-radius: 2px;
      }

      .iconfont {
        position: absolute;
        right: 0.26666667rem;
        top: 0px;
        /* transform: rotate(-90deg);*/
      }
    }
  }
  .foot {
    position: fixed;
    bottom: 0px;
    background-color: #fff;

    width: 100%;
    display: flex;

    .foot-btn {
      flex: 1;
      color: #ffffff;
      text-align: center;
      padding: 0.347rem 0;
      font-size: 0.427rem;
    }
    .cancel {
      color: #333333;
    }
    .confirm {
      color: #ffffff;
      background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    }
  }
}
</style>



